/* -------------------

------------------- */
Math.Calc = (function () {
  // obj
  function isInteger (obj) {
    return Math.floor(obj) === obj
  }

  /* -----------------------------------------------------
   3.14 >> 314 100
  @param floatNum {number} 
  @return ret {times:100, num: 314}
  ----------------------------------------------------- */
  function toInteger (floatNum) {
    var ret = { times: 1, num: 0 }
    var isNegative = floatNum < 0
    if (isInteger(floatNum)) {
      ret.num = floatNum
      return ret
    }
    var strfi = floatNum + ''
    var dotPos = strfi.indexOf('.')
    var len = strfi.substr(dotPos + 1).length
    var times = Math.pow(10, len)
    var intNum = parseInt(Math.abs(floatNum) * times + 0.5, 10)
    ret.times = times
    if (isNegative) {
      intNum = -intNum
    }
    ret.num = intNum
    return ret
  }

  /* -----------------------------------------------------
  
  

  @param a {number} 1
  @param b {number} 2
  @param digits {number}  2, 
  @param op {string} add/subtract/multiply/divide
  ----------------------------------------------------- */
  function operation (a, b, digits, op) {
    var o1 = toInteger(a)
    var o2 = toInteger(b)
    var n1 = o1.num
    var n2 = o2.num
    var t1 = o1.times
    var t2 = o2.times
    var max = t1 > t2 ? t1 : t2
    var result = null
    switch (op) {
      case 'add':
        if (t1 === t2) { // 
          result = n1 + n2
        } else if (t1 > t2) { // o1   o2
          result = n1 + n2 * (t1 / t2)
        } else { // o1   o2
          result = n1 * (t2 / t1) + n2
        }
        result = result / max
        break
      case 'subtract':
        if (t1 === t2) {
          result = n1 - n2
        } else if (t1 > t2) {
          result = n1 - n2 * (t1 / t2)
        } else {
          result = n1 * (t2 / t1) - n2
        }
        result = result / max
        break
      case 'multiply':
        result = (n1 * n2) / (t1 * t2)
        break
      case 'divide':
        result = (n1 / n2) * (t2 / t1)
        break
      default:
        result = 0
    }
    if (digits && !isNaN(digits)) {
      // 
      return toDigits(result, digits)
    }
    return result
  }

  // 
  function add (a, b, digits) {
    return operation(a, b, digits, 'add')
  }
  function subtract (a, b, digits) {
    return operation(a, b, digits, 'subtract')
  }
  function multiply (a, b, digits) {
    return operation(a, b, digits, 'multiply')
  }
  function divide (a, b, digits) {
    return operation(a, b, digits, 'divide')
  }
  // toFixed 
  function toFixed (num, digits) {
    var times = Math.pow(10, digits)
    var result = num * times + 0.5
    result = parseInt(result, 10) / times
    return result
  }
  // toDigits 
  function toDigits (argNum, digits) {
    if (Number(digits) === 0) return Math.floor(argNum)
    if (!digits) return argNum
    var digitsMatch = new RegExp('[0-9]+(\\.[0-9]{1,' + digits + '})?')
    var num = ('' + argNum).match(digitsMatch)
    if (num && num[0]) return Number(num[0])
    return argNum
  }
  // 
  function toThousandth (num) {
    if (!num) return '0'
    var sExpr = /^(\d+)((\.\d+)?)$/
    var n1 = num.toString().replace(sExpr, function (s, s1, s2) {
      var reExpr = /\d{1,3}(?=(\d{3})+$)/g
      return s1.replace(reExpr, '$&,') + s2
    })
    return n1
  }
  // exports
  return {
    add: add,
    subtract: subtract,
    multiply: multiply,
    divide: divide,
    toFixed: toFixed,
    toDigits: toDigits,
    toThousandth: toThousandth
  }
})();



